.userPhoto {
    width: 100px;
}

.selectedPage {
    font-weight: bold;

}

.pages {
    display: flex;
}

.pagesContainer {
    display: flex;

    .circle {
        height: 40px;
        width: 40px;
        border-radius: 50%;
        border: 1px solid black;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        margin-right: 10px;

        &:hover {
            background-color: black;
            color: white;
        }


    }
    .selectedPage {
        font-weight: bold;
        background-color: black;
        color: white;
    }
}